html, body {
  color: #f5f5f5;
  background-color: #262626;
  font-family: 'Montserrat', sans-serif;
}

#heading {
  text-align: center;
  font-weight: thinner;
}

#markdown-previewer {
  display: grid;
  grid-template-columns: 50% 50%;
 
}

#editor {
  justify-self: end;
  width: 70%;
  height: 80vh;
  outline: none;
  overflow: auto;
  color: #262626;
  background-color: #dcdcdc;
  border: 3px inset #F5F5F5;
  border-radius: 5px;
  margin: auto 20px;
  padding: 15px;
}

#preview {
  justify-self: start;
  width: 70%;
  height: 80vh;
  overflow: auto;
  color: #262626;
  background-color: #dcdcdc;
  border: 3px outset #F5F5f5;
  border-radius: 5px;
  margin: auto 20px;
  padding: 15px 25px;
}

#blockquote {
  background: #f9f9f9;
  border-left: 10px solid #262626;
  margin: .5em 10px;
  padding: 0.2em 10px;
  width: 300px;
}

